.nav_btn{
    background: #16c0ac;
    color: #0c3b98;
    padding: 4px 15px;
    font-size: 16px;
    border-radius:30px;
    cursor: pointer;
    border: 0;
    outline: 0;
    transition: 0.5s;
  }
  .nav_btn:hover{
    color: #fff;
    transform: scale(1.05);
    
  }

.nav-mob-open {
    width: 30px;
    display: none;
}

.nav-mob-close {
   display: none;
   background-color: #fff;
}

/* media query */
@media (max-width: 768px){
    .navbar{
        margin: 20px 3px;
        padding-bottom: 20px;
    }
    .nav_btn{
        display: none;
    }
    .nav-mob-open{
        display: block;
        position: fixed;
        right: 80px;
        cursor: pointer;
    }
    .nav-mob-close{
        display: block;
        position: relative;
        top: 30px;
        left: 250px;
        width: 30px;
    }
    .nav-menu{
        position: fixed;
        flex-direction: column;
        align-items: start;
        top: 0;
        gap: 30px;
        background-color: #1F0016;
        width: 350px;
        height: 100%;
        z-index: 2;
        transition: right 0.5s;
        right: -350px;
         cursor: pointer;
    }
    .nav-menu :hover{
        color: red
    }
    .nav-menu li{
        font-size: 30px;
        padding-left: 100px;
        flex-direction: row;
        gap: 20px;
    }
}

@media (max-width: 360px){
    .navbar{
        margin: 20px 3px;
